.page {
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
  background: #FFFFFF;
  font-family: "Open Sans", sans-serif;
}
@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: italic;
  font-display: optional;
  src: url("../fonts/opensansitalic.woff2") format("woff2"), url("../fonts/opensansitalic.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Display";
  font-weight: normal;
  font-style: normal;
  font-display: optional;
  src: url("../fonts/sfprodisplay.woff2") format("woff2"), url("../fonts/sfprodisplay.woff") format("woff");
}
.header {
  display: flex;
  flex-direction: column;
  margin: 12px 0 0;
  background: #FFFFFF;
  box-shadow: 0px 4px 48px rgba(0, 0, 0, 0.16);
}
.contacts-line {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  min-height: 32px;
  margin: 0;
  padding: 0 120px;
  background: #FED600;
  color: #000000;
  font-family: "Open Sans", "sans-serif";
  font-weight: 600;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
}
.contacts-line__elem {
  margin: 0 17px 0 0;
}
.contacts-line__elem:nth-child(4) {
  margin: 0 35px 0 auto;
}
.contacts-line__elem:nth-child(5) {
  margin: 0 25px 0 0;
}
.contacts-line__elem:nth-child(6),
.contacts-line__elem:nth-child(7) {
  margin: 5px 20px 0 0;
}
.contacts-line__elem:last-child {
  margin: 0;
}
.contacts-line__location {
  padding: 0 20px 0 0;
  background: none;
  border: none;
  outline: none;
  font: inherit;
  appearance: none;
  background: url("../img/tick.svg") no-repeat;
  background-position: top 6px right 10px;
  background-size: 10px 6px;
}
.contacts-line__contact-director,
.contacts-line__phone,
.contacts-line__write-us {
  color: inherit;
  text-decoration: none;
  text-align: center;
}
@media (max-width: 768px) {
  .contacts-line {
    padding: 0;
  }
}
.main-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-height: 137px;
  margin: 5px 0 0;
  padding: 0 120px;
  border-bottom: 1px solid #BDBDBD;
  color: #000000;
  font-family: "Open Sans", "sans-serif";
  font-weight: 600;
  font-size: 13px;
  line-height: 18px;
}
.main-header__logo {
  margin: 2px 0 0;
}
.main-header__quote {
  margin: 0 15px 0;
  padding: 0;
  text-align: start;
  font-style: italic;
}
.quote__3d {
  color: #D2001C;
}
.main-header__yandex-market {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 105px;
  height: 60px;
  margin: 5px 10px 0 20px;
  padding: 0;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.16);
}
.main-header__search {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 0 0 20px;
  width: 520px;
  border: 1px solid #BDBDBD;
  border-radius: 4px;
  font-family: Open Sans;
  font-size: 13px;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
}
.search__field {
  margin: 0;
  padding: 0 0 0 15px;
  width: 100%;
  outline: none;
  border: none;
  color: #000000;
  font-family: "Open Sans", "sans-serif";
  font-weight: 600;
  font-size: 13px;
  line-height: 18px;
}
.search__btn {
  margin: 4px;
  padding: 6px 13px;
  border: 2px solid #D2001C;
  border-radius: 4px;
  color: #D2001C;
  text-decoration: none;
}
.main-header__login {
  margin: 0 0 0 20px;
  padding: 5px 5px 5px 40px;
  background: url("../img/main-header__login.svg") 8px 3px no-repeat;
  background-size: 26px 22px;
  color: #000000;
  text-decoration: none;
}
.main-header__cart {
  margin: 0 0 0 25px;
  padding: 10px 10px 10px 50px;
  border-radius: 4px;
  background: #D2001C url("../img/main-header__cart.svg") 17px 9px no-repeat;
  background-size: 26px 22px;
  color: #FFFFFF;
  text-decoration: none;
}
.nav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  width: 100%;
  margin: 0 5px 15px;
}
.nav__link {
  color: #000000;
  text-decoration: none;
}
@media (max-width: 768px) {
  .main-header {
    padding: 0;
  }
}
.goods {
  display: flex;
  padding: 0 120px;
  min-height: 62px;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
}
.goods__link {
  padding: 10px 25px 10px 5px;
  color: #000000;
  text-decoration: none;
  font-family: Open Sans;
  font-size: 13px;
  font-weight: bold;
  line-height: 18px;
  background: url("../img/tick--grey.svg") no-repeat;
  background-position: top 16px right 12px;
  background-size: 10px 6px;
}
@media (max-width: 768px) {
  .goods {
    padding: 12px 0;
  }
}
.main {
  display: flex;
  flex-direction: column;
  margin: 70px 0 100px;
}
.intro {
  display: flex;
  padding: 0 106px;
  flex-flow: row wrap;
  justify-content: flex-start;
}
.intro__group-photo {
  width: 100%;
}
.intro__text {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.intro__header {
  margin: 50px 0 0;
  padding: 0;
  max-width: 333px;
  color: #000000;
  font-family: SF Pro Display;
  font-size: 27px;
  font-weight: 400;
  line-height: 32px;
}
.intro__resume {
  margin: 40px 0 0;
  padding: 13px 73px;
  background: #D2001C;
  border-radius: 4px;
  text-decoration: none;
  color: #FFFFFF;
  font-family: SF Pro Display;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  text-align: center;
}
.intro__personal-data {
  width: 230px;
  margin: 20px 0 0;
  padding: 0;
  font-family: SF Pro Display;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  color: #C4C4C4;
}
.intro__about-us {
  margin: 50px 0 0 auto;
  max-width: 708px;
  color: #4D4D4D;
  font-family: SF Pro Display;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
}
.intro__about-us--3d-print-awards {
  color: #D2001C;
  text-decoration: underline;
}
@media (max-width: 768px) {
  .intro {
    padding: 0;
  }
}
.links {
  display: flex;
  padding: 0 227px 0 106px;
  flex-flow: row wrap;
  justify-content: space-between;
}
.links__header {
  width: 100%;
  margin: 70px 0 0;
  font-family: SF Pro Display;
  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
  position: relative;
}
.links__header::after {
  content: "";
  position: absolute;
  top: 13px;
  right: -120px;
  display: block;
  width: 916px;
  height: 1px;
  background: #E5E5E5;
}
.links__link {
  margin: 48px 0 65px;
  color: #000000;
  text-decoration: none;
  border-bottom: 1px solid #000000;
  position: relative;
  font-family: SF Pro Display;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}
.links__link::after {
  content: "";
  position: absolute;
  display: block;
  top: 5px;
  right: -22px;
  width: 10px;
  height: 8px;
  background: url("../img/tick.svg") no-repeat;
  transform: rotate(270deg);
}
.links__link--red {
  color: #D2001C;
  border-bottom: 1px solid #D2001C;
}
.links__link--red::after {
  background: url("../img/tick--red.svg") no-repeat;
}
@media (max-width: 768px) {
  .links {
    padding: 0;
  }
}
.links__header::after {
  display: none;
}
.links__link::after {
  display: none;
}
.video {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 75px 135px 60px;
  background: #F9F9F9;
}
.video__header {
  display: flex;
  margin: 0;
  font-family: SF Pro Display;
  font-size: 36px;
  line-height: 43px;
}
.slider-controls {
  width: 80px;
  height: 39px;
  margin: 0 0 0 auto;
  display: flex;
  flex-flow: row;
  border: 1px solid #E5E5E5;
}
.slider-controls__btn {
  width: 40px;
  height: 39px;
  background: url("../img/slider-controls__btn.svg") 15px 10px no-repeat;
}
.slider-controls__btn:last-child {
  transform: rotate(180deg);
  border-right: 1px solid #E5E5E5;
}
.video__slider-body {
  display: flex;
  width: 100%;
  margin: 60px 0 0;
  flex-flow: row wrap;
  justify-content: space-between;
}
.video__slide {
  display: flex;
}
@media (max-width: 768px) {
  .video {
    padding: 75px 0 60px;
  }
}
